"use client";

import { Heart, Droplets, Thermometer, Flame, Activity } from "lucide-react";

export default function Dashboard() {
  return (
    <main className="min-h-screen bg-gray-100 p-4">
      {/* Top Section */}
      <div className="rounded-2xl bg-gradient-to-r from-indigo-500 to-purple-500 p-5 text-white shadow-md">
        <div className="flex justify-between items-start">
          <div>
            <h1 className="text-2xl font-bold">Beat Aware</h1>
            <p className="text-sm opacity-90">Real-time Health Monitor</p>
          </div>
          <div className="text-right">
            <p className="text-xs">Risk Score</p>
            <p className="text-xl font-bold">23%</p>
          </div>
        </div>

        <button className="mt-4 w-full bg-red-500 py-3 rounded-xl font-semibold shadow-md hover:bg-red-600">
          ðŸš¨ EMERGENCY
        </button>
      </div>

      {/* Live Vitals */}
      <section className="mt-6">
        <h2 className="text-lg font-bold text-gray-800 mb-3">
          Live Vitals
        </h2>
        <div className="grid grid-cols-2 gap-4">
          <VitalCard
            icon={<Heart className="w-6 h-6 text-red-500" />}
            label="Pulse Rate"
            value="88 bpm"
            status="Normal"
          />
          <VitalCard
            icon={<Droplets className="w-6 h-6 text-blue-500" />}
            label="Blood Oxygen"
            value="95 %"
            status="Warning"
          />
          <VitalCard
            icon={<Activity className="w-6 h-6 text-green-500" />}
            label="Blood Pressure"
            value="120/74 mmHg"
            status="Normal"
          />
          <VitalCard
            icon={<Thermometer className="w-6 h-6 text-orange-500" />}
            label="Body Temp"
            value="36.8 Â°C"
            status="Normal"
          />
          <VitalCard
            icon={<Thermometer className="w-6 h-6 text-pink-500" />}
            label="Sweat Temp"
            value="34.5 Â°C"
            status="Normal"
          />
          <VitalCard
            icon={<Flame className="w-6 h-6 text-yellow-500" />}
            label="Calories Burned"
            value="320 kcal"
            status="Normal"
          />
        </div>
      </section>

      {/* Quick Actions */}
      <section className="mt-6 space-y-4">
        <h2 className="text-lg font-bold text-gray-800">Quick Actions</h2>
        <div className="flex gap-4">
          <button className="flex-1 rounded-xl bg-purple-600 text-white py-3 font-semibold shadow hover:bg-purple-700">
            Book Appointment
          </button>
          <button className="flex-1 rounded-xl bg-white py-3 font-semibold shadow border border-gray-200">
            View History
          </button>
        </div>
      </section>

      {/* AI Health Insights */}
      <section className="mt-6 rounded-2xl bg-white p-5 shadow">
        <h2 className="text-lg font-bold text-gray-800 mb-3">
          AI Health Insights
        </h2>
        <ul className="space-y-2 text-sm text-gray-700">
          <Insight text="Heart rhythm is stable" />
          <Insight text="Stress levels are moderate â€“ consider a 5-min break" />
          <Insight text="Overall risk score: Low" />
        </ul>
      </section>

      {/* Bottom Navigation (optional) */}
      <nav className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 flex justify-around py-3">
        <NavItem label="Home" active />
        <NavItem label="History" />
        <NavItem label="Alerts" />
        <NavItem label="Settings" />
      </nav>
    </main>
  );
}

/* ---------- Small Components ---------- */
function VitalCard({
  icon,
  label,
  value,
  status,
}: {
  icon: React.ReactNode;
  label: string;
  value: string;
  status: string;
}) {
  return (
    <div className="rounded-2xl bg-white p-4 shadow text-center">
      <div className="flex justify-center mb-2">{icon}</div>
      <p className="text-lg font-bold text-gray-800">{value}</p>
      <p className="text-xs text-gray-500">{label}</p>
      <p
        className={`mt-1 text-xs font-semibold ${
          status === "Warning" ? "text-yellow-600" : "text-green-600"
        }`}
      >
        {status}
      </p>
    </div>
  );
}

function Insight({ text }: { text: string }) {
  return (
    <li className="flex items-start gap-2">
      <span className="mt-1 h-2 w-2 rounded-full bg-green-500" />
      <span>{text}</span>
    </li>
  );
}

function NavItem({ label, active }: { label: string; active?: boolean }) {
  return (
    <button
      className={`text-sm font-medium ${
        active ? "text-purple-600" : "text-gray-500"
      }`}
    >
      {label}
    </button>
  );
}
